<template lang='pug'>
#app
  .container.pt-3
    img-upload(:img='img',
      :is-uploading='isUploading',
      acceptFileType=".png,.jpeg",
      @img-upload="uploadImg",
      @img-delete="deleteImg",
      @size-err="sizeErr")
</template>

<script>
import 'bootstrap/dist/css/bootstrap.min.css'

export default {
  name: 'app',
  data () {
    return {
      img: { url: '' },
      isUploading: false,
    }
  },
  methods: {
    uploadImg (file) {
      console.log(file.name)
      this.isUploading = true
      setTimeout(()=>{
        this.isUploading = false
        this.img.url = 'https://zos.alipayobjects.com/rmsportal/jkjgkEfvpUPVyRjUImniVslZfWPnJuuZ.png'
      }, 2000)
    },
    deleteImg () {
      this.img.url = ''
      console.log('delete Img')
    },
    sizeErr () {
      console.error('size err')
    }
  }
}
</script>
